@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100..900&family=Noto+Sans:ital,wght@0,100..900;1,100..900&display=swap');
@import 'mixins';

$white-color1: #fff;
$white-color2: #f3f3f3;
$black-color1: #000;
$point-color1: #5593dd;

:root{
    --swiper-theme-color: #5593dd !important;
}

html,body {
    font-family: "Noto Sans KR";
    font-size: 16px;
    line-height: 1.4;
    opacity: 1;
}
img {max-width: 100%;}

::-webkit-scrollbar {
    width: 7px;
}
::-webkit-scrollbar-thumb {
    background: #111;
    border-radius: 20px;
}
::-webkit-scrollbar-track {
    background: #ebebeb;
    border-radius: 20px;
}


.blind {
    position: absolute;
    clip: rect(0 0 0 0);
    width: 1px;
    height: 1px;
    margin: -1px;
    overflow: hidden
}



header {
    
    position: absolute;
    top: 0;
    left: 0;
    display: flex    ;
    background: #fafafa;
    width: 60px;
    height: 100%;
    font-size: 13px;
    flex-direction: column;
    gap: 10px;
    z-index: 1;

    h1{
        font-size: inherit;
    }
    nav {
        padding: 70px 0 0;
        ul {
            display: flex;
            flex-direction: column;
            gap:20px;

            li {
                a {
                    display: block;
                    color:#000;
                    height: 50px;
                    text-align: center;
                    font-size: 0;
                    background: url("../public/images/icon/menu_2.png") no-repeat center center;
                    opacity: 0.2;
                }
                &:nth-child(2) {
                    a {
                        background-image: url("../public/images/icon/menu_1.png");
                    }
                }
                &.on {
                    a {
                        opacity: 1;
                    }
                }
            }
        }
    }
}/* E: header */


footer {
    font-size: 12px;
    text-align: center;
    opacity: 0.5;
    margin: 100px 0 0;
}/* E: footer */


.allAbout_wrap {
    position: relative;
    background: $white-color2;
    padding: 0 70px 200px 120px;
    min-height: 1200px;
} /* E: allAbout_wrap */


article {
    padding: 80px 0 0;
    section {
        position: relative;
        h2{
            position: absolute;
            top:0;
            left:0;
            font-size: 30px;
            font-weight: 900;
            line-height: 1;
        }
    }

    .tiny_notice {
        opacity: 0.5;
        margin: 0 0 10px;
        font-size: 14px;
    }

}/* E: article */


.compList_wrap {
    display: flex;
    flex-direction: row;
    gap: 20px;
    justify-content: center;
    margin: 0 auto 90px;
    li {
        a {
            display: block;
            padding: 5px;
            font-weight: bold;

            i {
                content:"";
                padding:5px 10px;
                color:#fff;
                background:#111
            }
        }
        &.on {
            a {
                font-weight: 900;
                border-bottom: 2px solid #111;
            }
        }
    }
}// compList_wrap




.case_link {
    position: absolute;
    top:0;
    right: 0;
    display: flex;
    align-items: center;
    gap: 10px;
    a {
        position: relative;
        display: flex;
        align-items: center;
        gap: 20px;
        font-weight: bold;
        &:not(.mode_select) {
            span {
                &.img {
                    width: 45px;
                    height: 45px;
                    img {
                        width: 100%;
                        animation: rott 15s linear infinite;
                    }
                }
                &:not(.img) {
                    position: relative;
                    padding-right: 7px;
                    &::before {
                        content: '';
                        position: absolute;
                        top: 2px;
                        right: 0px;
                        width: 5px;
                        height: 5px;
                        border-radius: 100%;
                        background: #111;
                    }
                } 
            }
        }
        img {
        width: 45px;
        height: 45px;
        object-fit: cover;
        border-radius: 100%;
        border: 1px solid #f3f3f3;
        background: #fff;
        }
        &.mode_select {
            svg {
                width: 30px;
                height: 30px;
                background: $white-color1;
                border-radius: 5px;
                path {
                    background-color: #000;
                    fill: #000;
                }

            }
        }
    }
}// case_link



/* profile */
.profile_zone {
    padding: 120px 0 0;
    .profile_wrap {
        position: relative;
        .prof_img {
            position: absolute;
            top: -30px;
            left: 170px;
            width: 425px;
            z-index: 1;
            .side_deco {
                position: absolute;
                bottom: 160px;
                left: -280px;
                transform: rotate(270deg);
                font-weight: 900;
                font-size: 50px;
                z-index: 1;
            }
            .img_wrap {
                position: relative;
                display: flex;
                align-items: flex-end;
                justify-content: flex-start;
                img {
                    border-radius:  240px 240px 260px 0;
                }
            }
            &::before {
                content: '';
                position: absolute;
                top: 100%;
                left: 100%;
                width: 100px;
                height: 100px;
                margin: -50px 0 0 -70px;
                border:2px dashed #000;
                border-radius: 100%;
                display: none;
            }
            p {
                position: relative;
                margin: 20px 40px 0 0;
            }
        }
    }
    
    .intro_wrap {
        position: relative;
        padding: 80px 80px 80px 670px;
        background: linear-gradient(180deg, #ffffff 0%, #f3f3f3 100%);
        border-radius: 20px;
        box-shadow: 0 0 5px -5px rgba(0, 0, 0, 0.05);
        h3 {
            font-size: 24px;
            font-weight: bold;
        }
        .info_list {
            text-align: left;
            margin: 10px 0 40px;
            .item {
                & ~ .item{
                    margin: 20px 0 0;
                }
            }
            p {
                position: relative;
                display: flex;
                flex-direction: row;
                flex-wrap: wrap;
                justify-content: flex-start;
                font-weight: 600;
                & ~ p{
                    margin: 5px 0 0;
                }
                em {
                    color: $point-color1;
                    font-weight: inherit;
                    flex: 0 0 160px;
                }
                &.detail {
                    font-size: 14px;
                    padding: 5px 0 0 0;
                    color: #404040;
                    font-weight: 400;

                }
            }
        }
    }
}/* E: profile_wrp */

@keyframes rott {
    to {
        transform: rotate(360deg);
    }
}

/* portfolio */
.portfolio_wrap {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
    
    .work_history {
        position: relative;
        display: flex;
        flex-wrap: wrap;
        align-items: stretch;
        flex-direction: row;
        justify-content: flex-start;
        align-content: stretch;
        background: linear-gradient(180deg, #ffffff 0%, #f3f3f3 100%);
        width: 100%;
        /* min-height: 400px; */
        border-radius: 20px;
        box-shadow: 0 0 5px -5px rgba(0, 0, 0, 0.05);
        &::after {
            display: none;
            content: '';
            position: absolute;
            top: 0;
            left: 220px;
            height: 100%;
            text-align: left;
            color: #fff;
            background: linear-gradient(180deg, transparent 0%, #f3f3f3 100%);
            width: 30px;
            height: 110%;
        }
        .intro {
            position: relative;
            top: -30px;
            display: flex;
            flex-wrap: wrap;
            box-shadow: 0 0 5px -5px rgba(0, 0, 0, 0.05);
            flex-direction: column;
            justify-content: flex-start;
            align-content: flex-start;
            width: 300px;
            text-align: left;
            color: #fff;
            background: linear-gradient(180deg, $point-color1 20%, #f0f5c0 70%, #f3f3f3 100%);
            border-radius: 0 120px 0 0;
            padding: 100px 0 0 35px;
            h3 {
                position: relative;
                margin: 0 0 30px;
                font-size: 18px;
            }
            dl {
                display: flex;
                flex-direction: column;
                gap: 5px;
                dt {
                    position: relative;
                    font-weight: bold;
                    padding-left: 12px;
                    line-height: 1;

                    &::after {
                        content: '';
                        position: absolute;
                        top: 10px;
                        left: 0;
                        width: 7px;
                        height: 2px;
                        background: $white-color1;
                        transform: rotate(-45deg);
                    }
                }
                dd {
                    padding-left: 12px;

                    span {
                        display: block;
                        font-size: 13px;
                        padding: 5px 0 0;
                        opacity: 0.6;
                    }
                    &+dt {
                        margin-top: 10px;
                    }
                }
            }
        }
        .history {
            padding: 70px 40px 200px 40px;
            flex: 1;
            h3 {
                margin-top: 40px;
            }
            ul {
                display: flex;
                flex-direction: column;
                gap: 5px;
                li {
                    position: relative;
                    display: flex;
                    flex-direction: row;
                    padding: 0 0 0 12px;
                    &::before {
                        content: '';
                            position: absolute;
                            top: 10px;
                            left: 0;
                            width: 7px;
                            height: 2px;
                            background: $black-color1;
                            transform: rotate(-45deg);
                    }
                    span {
                        width: 60px;
                    }
                }
            }
            dl {
                margin: 50px 0 0;
                dt {
                    font-weight: bold;
                    margin: 20px 0 5px;
                }
            }
        }
    }
    .portList_wrap {
        width: 100%;
        & ~ .portList_wrap {
            margin-top: 70px;
        }
        h3{
            position: relative;
            font-size: 23px;
            margin-bottom: 30px;
            line-height: 1;
        }
        .port_list {
            position: relative;
            overflow: hidden;
            padding: 10px;
            margin: 0 -10px;
            .swiper-wrapper {
                display: flex;
                align-items: stretch;
            }
            .swiper-slide {
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
            }
            .swiper-pagination {
                position: relative;
                padding: 40px 0 0;
            }
            .item {
                width: 100%;
                background-color: #fff;
                border-radius: 15px;
                overflow: hidden;
                box-shadow: 5px 5px 10px -5px rgba(0, 51, 105, 0.1);
            }
            a {
                display: block;
                &:not(.onair) {
                    cursor: default;
                }    
            }
            .dp_wrap {
                position: relative;
                .scroll_box {
                    height: min(30vw, 600px);
                    overflow: hidden;
                    border-radius: 1rem;
                    &::-webkit-scrollbar {
                        width: 3px;
                    }
                    &::-webkit-scrollbar-thumb {
                        background: #888;
                    }
                }
                img {
                    width:560px;
                }
                &::before {
                    content: '';
                    width: 100%;
                    height: 20%;
                    position: absolute;
                    bottom: 0;
                    left: 000;
                    background: linear-gradient(360deg, white, transparent);
                }
            }
            .info {
                display: flex;
                flex-direction: column;
                gap:10px;
                padding: 20px;
                .name {
                    font-size: 18px;
                    margin: 0 0 10px;
                }
                .detail {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    flex-wrap: wrap;
                    gap: 10px;
                    .invol {
                        width: 100%;
                    }
                    span:not(.numb) {
                        display: inline-flex;
                        justify-content: center;
                        align-items: center;
                        padding: 2px 7px;
                        border-radius: 3px;
                        background: #f0f5c0;
                        margin: 0 6px 0 0;
                        font-size: 13px;

                        &.manage {
                            background: #e1e9f9;
                        }
                        &.design {
                            background: #f9e1f1;
                        }
                        &.state {
                            display: inline-flex;
                            font-size: 13px;
                            background: #e7e7e7;
                            border-radius: 10px;
                            padding: 1px 9px 2px;
                            margin: 0 0 0 10px;
                        }
                    }
                }
            }
            &.depth {
                display: flex;
                flex-direction: row;
                flex-wrap: wrap;
                align-items: flex-start;
                justify-content: flex-start;
                align-items: stretch;
                gap: 4%;

                .item {
                    width: 30.3%;
                    margin: 0 0 4%;
                }
                .dp_wrap {
                    .scroll_box {
                        height: auto;
                    }
                }
            }
        }
    }
}/* E: portfolio_wrap */

@include respond-to('xlg-desktop') {
    br.hide-lgdesk {
        display: none;
    }
}/* E: xlg-desktop */

@include respond-to('lg-desktop') {
    br.hide-lgdesk {
        display: block;
    }
    /* profile */
    .profile_zone {
        .profile_wrap {
            .prof_img {
                width: 30%;
                left: 8%;
                .side_deco {
                    font-size: clamp(20px, 4.0vmin, 50px);
                    line-height: 1.2;
                    left: -15%;
                    bottom: 65%;
                    transform: rotate(0deg);
                    z-index: 2;
                }
            }
        }
        .intro_wrap {
            padding: 60px 5% 60px 42%;
            h3 {
                font-size: clamp(17px, 2.7vmin, 22px);
            }
        }
    }/* E: profile_wrp */
    /* portfolio */
    .portfolio_wrap {
        .portList_wrap {
            .port_list {
                &.depth {
                    .info {
                        .detail {
                            .numb {
                                display: none;
                            }
                        }
                    }
                }
            }
        }
    }/* E: portfolio_wrap */
}/* E: lg-desktop */

@include respond-to('sm-desktop') {
    html,body {
        font-size: clamp(14px, 2.1vmin, 16px);
    }
    .allAbout_wrap {
        padding: 0 0 150px 0;
    } /* E: allAbout_wrap */
    article {
        padding: 50px 50px 0;
        section {
            h2{
                font-size: clamp(22px, 3.5vmin, 30px)
            }
        }
    }/* E: article */
    .compList_wrap {
        margin-bottom: 50px;
        width: 100%;
    }// compList_wrap
    header {
        position: fixed;
        height: auto;
        flex-direction: row;
        top: inherit;
        bottom: 0;
        left: 0;
        width: 100%;
        box-shadow: -10px 0px 20px 0px rgba(0,0,0,0.15);
        z-index: 2;
        nav {
            width: 100%;
            padding: 0;
            ul {
                flex-direction: row;
                gap: 20px;
                justify-content: center;
                li {
                    flex: 1;
                    a {
                        display: block;
                        width: 100%;
                        padding: 60px 0 0;
                        opacity: 1;
                    }
                    &.on {
                    }
                }
            }
        }
    }/* E: header */

    /* profile */
    .profile_zone {
        padding: 80px 0 0;
        .intro_wrap {
            .info_list {
                p {
                    flex-direction: column;
                    br {
                        display: none;
                    }
                    em {
                        position: relative;
                        flex: none;
                        padding: 0 0 0 12px;
                        &::before {
                            content: '';
                            position: absolute;
                            top: 10px;
                            left: 0;
                            width: 7px;
                            height: 2px;
                            background: $point-color1;
                            transform: rotate(-45deg);
                        }
                    }
                }
            }
        }
    }/* E: profile_wrp */

    /* portfolio */
    .portfolio_wrap {
        .work_history {
            .intro {
                width: 220px;
                padding: 70px 0 0 20px;
                top: -20px;
            }
            .history {
                padding: 50px 5% 100px 5%;
            }
        }
        .portList_wrap {
            h3{
                font-size: clamp(17px, 2.8vmin, 23px);
                margin-bottom: 20px;
            }
            .port_list {
                .swiper-pagination {
                    padding: clamp(10px, 3.5vmin, 30px) 0 0;
                }
                .info {
                    .name {
                        font-size: clamp(14px, 2.3vmin, 18px);
                        margin: 0;
                    }
                }
                &.depth {
                    gap: 15px;
                    .item {
                        width: calc(33.3% - 10px);
                    }
                }
            }
        }
    }/* E: portfolio_wrap */
}/* E: sm-desktop */

@include respond-to('tablet') {
    .allAbout_wrap {
        min-height: auto;
        font-size: 15px;
        padding: 30px 5% 80px;
    } /* E: allAbout_wrap */

    article {
        padding: 0;
    }/* E: article */

    .case_link {
        position: relative;
        margin-left: auto;
        top: inherit;
        right: inherit;
        display: inline-flex;
        float: right;
    }// case_link

    /* portfolio */
    .portfolio_wrap {
        .work_history {
            .intro {
                width: 220px;
                padding: 70px 0 0 25px;
                h3 {
                    font-size: clamp(15px, 2.3vmin, 18px);
                }
                dl {
                    flex: 1;
                }
            }
            .history {
                padding: 40px 30px 70px 30px;
                dl {
                    margin: 30px 0 0;
                }
            }
        }
        .portList_wrap {
            & ~ .portList_wrap {
                margin-top: clamp(30px, 7.5vmin, 70px);
            }
            .port_list {
                .info {
                    padding: 15px 5%;
                }
                &.depth {
                    .item {
                        width: calc(50% - 10px);
                    }
                }
            }
        }
    }/* E: portfolio_wrap */
}/* E: tablet */

@include respond-to('mobile') {
    header {
        nav {
            ul {
                li {
                }
            }
        }
    }/* E: header */

    .allAbout_wrap {
        font-size: 13px;
    } /* E: allAbout_wrap */
    .compList_wrap {
        margin-bottom: 20px;
        
    }// compList_wrap

    .case_link {
        a {
            &:not(.mode_select) {
                span {
                    &.img {
                        img {
                        }
                    }
                    &:not(.img) {
                        &::before {
                        }
                    } 
                }
            }
            img {
            }
            &.mode_select {
                svg {
                    path {
                    }
                }
            }
        }
    }// case_link

    /* profile */
    .profile_zone {
        padding: 80px 0 0;
        .profile_wrap {
            .prof_img {
                position: relative;
                top: inherit;
                left: inherit;
                width: auto;
                gap: 3%;
                margin: 0 0 7%;
                padding: 0 35% 0 0;
                .side_deco {
                    position: relative;
                    bottom: inherit;
                    left: inherit;
                    display: flex;
                    flex-direction: row;
                    align-items: flex-end;
                    padding: 0 0 0;
                }
                .img_wrap {
                    border-radius: 47% 47% 50% 0;;
                    overflow: hidden;
                    position: absolute;
                    top: 7%;
                    right: 0;
                    width: clamp(180px, 30vmin, 280px);
                    img {
                        border-radius:  0;
                    }
                }
                p {
                    margin: 5% 0 0;
                    grid-area: 2 / 2 / 4 / 3;
                    display: flex;
                    flex-direction: row;
                    align-items: flex-start;
                }
            }
        }
        .intro_wrap {
            padding: 30px 5% 80px 5%;
        }
    }/* E: profile_wrp */
    /* portfolio */
    .portfolio_wrap {
        .work_history {
            background: linear-gradient(180deg, $point-color1 150px, #fcffdb 250px, #f3f3f3 100%);
            margin-bottom: 70px;
