    article {
        section {
            h2{
            }
        }

        .tiny_notice {
            font-size: 12px;
        }

    }/* E: article */

    /* portfolio */
    .portfolio_wrap {
        .work_history {
            .intro {
                flex-direction: column;
                padding-top: 20px;
                h3 {
                    margin: 0 0 15px;
                }
                dl {
                    padding-top: 3px;
                    dd {
                        flex-direction: row;
                        span {
                            padding: 0 0 0 5px;
                            br {
                                display: none;
                            }
                        }
                    }
                }
            }
        }

        .portList_wrap {
            h3{
                margin-bottom: 15px;
            }
            .port_list {
                .info {
                    .detail {
                        font-size: 12px;
                        span:not(.numb) {
                            font-size: 11px;
                            padding: 2px 3px;
                            &.state {
                                font-size: 11px;
                                padding: 2px 3px;
                                border-radius: 3px;
                            }
                        }
                        span.numb {
                            display: none;
                        }
                    }
                }
            }
        }
    }/* E: portfolio_wrap */
}/* E: sm-mobile */
